import helper from '../../common/helper.js';
export default {
	components: {},
	data() {
		return {
			UserId: '',
			userfoArray: '',
			nickname: '',
			mobile: '',
			userdata: "",
			loginMark: "",
			token: "",
			Headimgurl: '',
			Position:'',
			img:'',
			imgss:'',
			MerImg:'',
			ArtiPerson:'',
			MerPer:'' , //法人名称
			MerName:'' ,//商家名称
			
		};
	},
	onLoad() {
		this.userinfo();
		this.UserId = helper.getstate().userid;
		this.loginMark = uni.getStorageSync('loginMark')
		this.token = uni.getStorageSync('token')
	},
	methods: {
		// 获取个人信息
		userinfo() {
			uni.showLoading({
				title:'加载中'
			});
			var userId = helper.getstate().userid;
			uni.request({
				url: helper.websiteUrl + 'merchantcenter/getmerchantinfo',
				method: 'GET',
				data: helper.postdata({
					mid: userId,
				}),
				success: res => {
					if (res.data.code == 200) {
						this.img = res.data.data.baseinfo.Headimgurl;
						this.userfoArray = res.data.data.baseinfo;
						this.imgss = this.userfoArray.BusinessLicense;
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		setData: function(obj) {
			let that = this;
			Object.keys(obj).forEach(function(key) {
				that.$set(that.$data, key, obj[key])
			});
		},
		// 获取用户上传头像地址
		getImage: function() {
			var _this = this
			uni.chooseImage({
				success: function(res) {
					uni.showLoading({
						title: '上传中'
					});
					uni.uploadFile({
						url: helper.websiteUrl + '/annexes/UploadImg', 
						filePath: res.tempFilePaths[0],
						fileType: "uploadFile",
						name: 'upload',
						formData: helper.postdata({
							
						}),
						success: function(resdata) {
							var resdatas = JSON.parse(resdata.data);
							if (resdatas.code == 200) {
								var url = 'https://cszx.yiruit.net';
								var imgurls = url + resdatas.info;
								_this.Headimgurl = imgurls;
								uni.showToast({
									icon: 'none',
									title: '上传成功'
								});
							} else{
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: '上传失败,请重试'
								});
							}
							uni.hideLoading();
						},
						fail() {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '上传失败,请重试'
							});
						}
					})
					_this.setData({
						img: res.tempFilePaths[0],
					})
				},
			})
		},
		// 营业执照识别
		DiscernImage: function() {
			var _this = this;
			uni.chooseImage({
				success: function(res) {
					_this.imgss = res.tempFilePaths[0];
					uni.showLoading({
						title: '识别中'
					});
					uni.uploadFile({
						url: helper.websiteUrl + 'annexes/businesslicense',
						filePath: res.tempFilePaths[0],
						fileType: "uploadFile",
						name: 'upload',
						formData: {
							loginMark: helper.getloginMark(),
							token: '',
							data: '{}'
						},
						success: function(resup) {
							var resdata = JSON.parse(resup.data);
							if (resdata.code == 200) {
								var url = 'https://cszx.yiruit.net';
								var imgurls = url + resdata.data.path;
								_this.MerImg = imgurls;
								_this.MerName = resdata.data.name;    // 商家名称
								_this.MerPer = resdata.data.person;    // 法人名称
								uni.showToast({
									icon: 'none',
									title: '识别成功！'
								});
							} else {
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: resdata.info
								});
							}
							uni.hideLoading();
						},
						fail() {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '识别失败'
							});
						}
					});
					_this.setData({
						imgss: res.tempFilePaths[0],
					});
				},
			})
		},
		// 修改个人信息表单提交
		formSubmit: function(e) {
			var _this = this
			uni.showModal({
				title: '提示',
				content: '是否确定修改！',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '修改中'
						});
						_this.userdata = e.detail.value
						uni.request({
							url: helper.websiteUrl + '/merchantcenter/updatemerchantcenter',
							method: 'GET',
							data: {
								"loginMark": _this.loginMark,
								"token": _this.token,
								"data": _this.userdata
							},
							success: res => {
								if (res.data.code == 200) {
									uni.showToast({
										icon: 'none',
										title: '修改成功'
									});
								} else {
									uni.showToast({
										icon: 'none',
										title: res.data.info
									});
								}
								uni.hideLoading();
							},
							fail: () => {
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: '网络异常,请稍后重试'
								});
							}
									
						});
					} 
				}
			});
			
		},
		openphone() {
			uni.navigateTo({
				url: 'zhanghuanquan',
			});
		}
	}
}
